import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from 'react-router-dom';
import shoesData from './data.js';
import styled from 'styled-components';
import './Detail.scss';


function Detail(props) {

    let [alertCondition, alertConditionFuc] = useState(true);

    useEffect(()=>{
        // alert window disable after 2 sec
        let timer = setTimeout(()=>{
            alertConditionFuc(false);
        }, 2000);
        return ()=>{
            clearTimeout(timer);
        }
    },[alertCondition]);    // [] -> excute only one time

    let { id } = useParams();
    let history = useHistory();

    let foundProduct = props._shoes.find(function(shoesData){
        return shoesData.id == id;
    });

    return (
        <div className="container">
            <h4 className="red">Detail</h4>

            {
                alertCondition === true
                ? (<div className="my-alert2"><p>재고가 얼마 남지 않았습니다</p></div>)
                : null
            }

            <div className="row">
                <div className="col-md-6">
                    <img src={'https://codingapple1.github.io/shop/shoes' + (parseInt(id)+1) + '.jpg'} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{foundProduct.title}</h4>
                    <p>{foundProduct.content}</p>
                    <p>{foundProduct.price}</p>
                    <InventoryInfo _inventory={props._inventory} _key={id} />
                    <button className="btn btn-danger" onClick={()=>{
                        let newInventory = [...props._inventory];
                        newInventory[id]--;
                        props._inventoryFuc(newInventory);
                    }}>주문하기</button>
                    &nbsp;
                    <button className="btn btn-danger" onClick={()=>{
                        history.goBack();
                    }}>뒤로가기</button>
                </div>
            </div>
        </div> 
    );
}

function InventoryInfo(props) {
    return (
        <p>재고 : {props._inventory[props._key]}</p>
    )
}

export default Detail;